<template>
  <div class="extensiondetail">
   <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     推广素材
  </div>
  <div class="extension_nav">分享下述文章，可帮助获得客户，锁定粉丝</div>
  <iframe :src="url_iframe"  id="url-iframe" frameborder="0" ></iframe>
 <div class="widthdraw_tx" @click="appShare">立即分享</div>
  </div>
</template>

<script>
export default {
  name: 'extensiondetail',
  data () {
    return {
     url_iframe:this.$route.query.index
    }
  },
  created(){
      
  },
  methods:{
    appShare:function(){
      let shareUrl = this.$route.query.index
      let shareTitle = this.$route.query.title
      var postData = {
              action:"share",
              params:{
                shareUrl:shareUrl,
                shareTitle:shareTitle
              }
          }
      if (window.postMessage) {
          window.postMessage(JSON.stringify(postData));
      }

}
  }
}

</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.extensiondetail{
  width:100vw;
  min-height:100vh;
  background:#ffffff;
  .px2rem(font-size,32);
  color:#2E2E2E;
  position: fixed;
}
.extension_nav{
    width:100%;
    .px2rem(height, 70);
     .px2rem(line-height, 70);
     background:#F0F0F0 ;
     text-align: center;
       .px2rem(font-size,26);
         color:#FF532A ;
}
.extension_content{
border-bottom:1px solid #d1d1d6;
width:90%;
margin:0 auto;
.px2rem(padding-top, 44);
.px2rem(padding-bottom, 10);
text-align: center;
span{
  display: block;
  width:100%;
   .px2rem(font-size,22);
    color:#8E8E93 ;
    .px2rem(padding-top, 20);
.px2rem(padding-bottom, 5);
}
}
.extension_text{
  .px2rem(padding-bottom, 140);
}
.widthdraw_tx{
  width:100%;
  .px2rem(height, 120);
  .px2rem(line-height, 120);
  text-align: center;
  background-color:#007AFF;
  position: fixed;
  z-index: 99;
  bottom:0;
  left:0;
  color:#ffffff;
  .px2rem(font-size, 34)
}
#url-iframe{
	width:100vw;
	display: block;
	border:0;
	height:80vh;
	.px2rem(padding-bottom,120);
}
</style>



