import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import promote from '@/components/promote'
import capital from '@/components/capital'
import order from '@/components/order'
import recharge from '@/components/recharge'
import user from '@/components/user'
import withdrawals from '@/components/withdrawals'
import addaccount from '@/components/addaccount'
import banklist from '@/components/banklist'
import bankdetail from '@/components/bankdetail'
import invitedqr from '@/components/invitedqr'
import extension from '@/components/extension'
import extensiondetail from '@/components/extensiondetail'
import team from '@/components/team'
import service from '@/components/service'
import progres from '@/components/progres'
import result from '@/components/result'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    }, 
     {
      path: '/promote',
      name: 'promote',
      component: promote
    },
    {
      path: '/capital',
      name: 'capital',
      component: capital
    },
    {
      path: '/order',
      name: 'order',
      component: order
    },
    {
      path: '/recharge',
      name: 'recharge',
      component: recharge
    },
    {
      path: '/user',
      name: 'user',
      component: user
    },
    {
      path: '/withdrawals',
      name: 'withdrawals',
      component: withdrawals
    },
    {
      path: '/addaccount',
      name: 'addaccount',
      component: addaccount
    },
    {
      path: '/banklist',
      name: 'banklist',
      component: banklist
    },
    {
      path: '/bankdetail',
      name: 'bankdetail',
      component: bankdetail
    },
    {
      path: '/invitedqr',
      name: 'invitedqr',
      component: invitedqr
    },
    {
      path: '/extension',
      name: 'extension',
      component: extension
    },
    {
      path: '/extensiondetail',
      name: 'extensiondetail',
      component: extensiondetail
    },
    {
      path: '/team',
      name: 'team',
      component: team
    }, {
      path: '/service',
      name: 'service',
      component: service
    },
     {
      path: '/progres',
      name: 'progres',
      component: progres
    },
     {
      path: '/result',
      name: 'result',
      component: result
    }
  ]
})



