<template>
  <div class="addaccount">
    <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     添加收款账户
  </div>
   <div class="addaccount1">请务必使用自己的真实信息</div>
       <ul class="addaccount5">
         <li>
           <span>姓名</span><input type="text" v-model="name">
         </li>
         <li>
         <span>卡号</span><input type="number" placeholder="请填写储蓄卡卡号"  v-model="bankCard">
         </li>
         <li>
        <span>验证码</span>   <input type="number" name="" id="" maxlength="6" v-model="smsCode">  <i @click="smscode()"> <toast  :msg=msg ref='toast'></toast></i>
         </li>
       </ul>
     <div class="btn" @click="preserve">确定</div>
      
  </div>
</template>

<script>
import toast from './toast.vue'
import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'addaccount',
   components:{toast},
  data () {
    return {
      msg: '',
      name:'',
      bankCard:'',
      smsCode:""
    }
  },
  methods:{
    preserve:function(){
    	 var that=this;
    	   if(this.name==''){
    	   	 this.$refs.toast.sign("请输入姓名");
    	   	return false;
    	   }
    	    if(this.bankCard==''){
    	   	 this.$refs.toast.sign("银行卡号");
    	   	return false;
    	   }
    	    if(this.smsCode==''){
    	   	 this.$refs.toast.sign("请输入验证码");
    	   	return false;
    	   }
    	    //添加银行卡账号
    	  get("/wc_app/add_cash_bank_card",{"smsCode":this.smsCode,"bankCard":this.bankCard,"name":this.name}).then(res=>{
    		if(res.data.status==200){
    	    this.$refs.toast.sign("添加成功");
        setTimeout(function(){
         that.$router.go(-1);
        },2500)
    		}else{
    			 this.$refs.toast.sign(res.data.msg);
    		}
	}).catch((res)=>{
		console.log(res)
	})

//   this.$router.push({path:'/'});
    },
      //获取验证码
  smscode:function(){
  	  this.$refs.toast.message();
  }
  }

}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.addaccount{
  width:100vw;
  height:100vh;
  background:#ffffff;
}
.addaccount1{
     width:100%;
    .px2rem(height, 80);
     .px2rem(line-height, 80);
    .px2rem(padding-left, 40);
    .px2rem(font-size, 24);
    color:#8E8E93;
    background-color:#F0F0F0;
}

.addaccount5{
  width:100%;
  height:auto;
  overflow: hidden;
  border-top:1px solid #d1d1d6;
  border-bottom:1px solid #d1d1d6;
  li{
    .px2rem(width, 710);
    .px2rem(height, 120);
     .px2rem(line-height, 120);
    .px2rem(margin-left, 40);
     border-bottom:1px solid #d1d1d6;
     overflow: hidden;
      .px2rem(font-size, 28);
     span:nth-of-type(1){
       color:#2E2E2E;
      .px2rem(width, 138);
      float: left;
     }
     input{
       float: left;
       outline: #007AFF;
       .px2rem(height, 120);
       color:#8E8E93;
        .px2rem(width, 300);
     }
     i{
       float:right;
        .px2rem(margin-right, 30);
        font-style:normal;
        color:#007AFF;
     }
  }
  li:last-child{
    border:0;
  }
}
.addaccount5_img{
   .px2rem(width, 80);
    .px2rem(height, 80);
    .px2rem(margin-top, 10);
    .px2rem(margin-right, 30);
    float:right;
}
 .btn{
  opacity: 0.6;
  }
</style>



