<template>
  <div class="user">
  <div class="pub_header">补全信息</div>
   <div class="user1">请务必使用自己的真实信息</div>
    <div class="user2">该信息暂时不能修改，请特别注意</div>

     <div class="user3" @click="appUploadImage">上传<br>微信二维码
        <img :src="img_url" alt="" class="user_img" />
     </div>
    <div class="user4">上传自己的微信二维码</div>
 
       <ul class="user5"> 
         <li>
           <span>微信昵称</span><span>{{msg_data.nikename}}</span><img :src="msg_data.picUrl" alt="" class="user5_img">
         </li>
         <li>
         <span>真实姓名</span><span>{{msg_data.name}}</span>
         </li>
         <li>
         <span>手机号码</span><span>{{msg_data.mobile}}</span>
         </li>
         <li>
        <span>验证码</span>   <input type="number" name="" id="" maxlength="6" v-model="sms">  <i @click="smscode()">   <toast  :msg=msg ref='toast'></toast></i>
         </li>
       </ul>
     <div class="btn" @click="preserve">保存</div>
   
  </div>
</template>

<script>
import toast from './toast.vue'
import {get,uploadQiniu}  from '../utils'
import Vue from 'vue'
export default {
  name: 'user',
  components:{toast},
  data () {
    return {
      msg: '',
      img_url:'',
      sms:'',
      flag:true,

      msg_data:"",
      sc:false,
      imagefile:null
    }
  },
  created(){
  	var that=this;
     get("/wc_app/query_cust_info").then(function(res){
     	if(res.data.status==200){
     		that.msg_data=res.data.data;
     		console.log(res.data.data);
     	}
     })
 var that=this;
    document.addEventListener('message', function(msg) {
        that.img_url=msg.data;
       
    })
    
  },
  methods:{
    preserve:function(){
    	var that=this;
    	if(this.flag){
    		this.flag=false
    	
    	if(this.img_url==""){
    		this.$refs.toast.sign("请上传图片");
    		return false;
    	}
    	if(this.sms==""){
    		that.$refs.toast.sign("请输入验证码");
    		return false;
    	}
    	 get("/wc_app/save_wechat_qr",{"url":this.img_url,"smsCode":this.sms}).then(function(res){

    	 	if(res.data.status==200){
    	 		that.$refs.toast.sign("保存成功");
    	 		   setTimeout(function(){
         that.$router.push({path:'/'});
        },2000)
    	 	}else{
    	 		that.$refs.toast.sign(res.msg);
    	 	}
    	 	
    	 }).catch(function(res){
    	 	console.log(res)
    	 })
    	 this.flag=true;
    }
    },
    
    appUploadImage:function(){
      var postData = {
              action:"upload",
              params:{

              }
          }
      if (window.postMessage) {
          window.postMessage(JSON.stringify(postData));
      }
    },

    //上传图片
      uploadImage1: function(event) {
       
    		var self = this
        var file = document.getElementById("file").files[0]; 

        var formdatas=new FormData();// 创建form对象  uploadFile
        formdatas.append('uploadFile',file);// 通过append向form对象添加数据,可以通过append继续添加数据

        console.log(formdatas.get('uploadFile'))
        var config = {  
            headers:{'Content-Type':'multipart/form-data'}  
        };  
        uploadQiniu('/wc_app/add_wechat_qr',formdatas,config).then(function(res){   //这里的/xapi/upimage为接口  
           
            console.log(res)
            if(res.data.status==200){
              self.img_url=res.data.data.url;
              console.log(self.img_url)
            }
        })  
      },
      //获取验证码
      smscode:function(){
          this.$refs.toast.message();
      }
  }


}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.user{
  width:100vw;
  height:100vh;
  background:#ffffff;
}
.user1{
     width:100%;
    text-align: center;
    .px2rem(padding-top, 70);
    .px2rem(padding-bottom, 4);
    .px2rem(font-size, 40);
    color:#000000;
}
.user2{
     width:100%;
    text-align: center;
    .px2rem(padding-bottom, 56);
    .px2rem(font-size, 26);
    color:#FF8D00 ;
    
}
.user3{
  .px2rem(width, 270);
  .px2rem(height, 270);
  background-color: #D8D8D8;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin:0 auto;
  border-radius: 10px;
  position: relative;
  color:#ffffff;
  .px2rem(font-size,34);
  input{
    opacity: 0;
    position: absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    z-index: 9999;
  }
}
.user_img{
	.px2rem(width, 260);
  .px2rem(height, 260);
  display: block;
  position: absolute;left:5px;top:5px;
  z-index: 99;
}
.user4{
    width:100%;
    text-align: center;
     .px2rem(padding-top, 8);
    .px2rem(padding-bottom, 26);
    .px2rem(font-size, 24);
    color:#2E2E2E;
}
.user5{
  width:100%;
  height:auto;
  overflow: hidden;
  border-top:1px solid #d1d1d6;
  border-bottom:1px solid #d1d1d6;
  li{
    .px2rem(width, 710);
    .px2rem(height, 100);
     .px2rem(line-height, 100);
    .px2rem(margin-left, 40);
     border-bottom:1px solid #d1d1d6;
     overflow: hidden;
      .px2rem(font-size, 28);
     span:nth-of-type(1){
       color:#2E2E2E;
      .px2rem(width, 138);
      float: left;
     }
       span:nth-of-type(2){
       color:#8E8E93;
      .px2rem(width, 138);
      float: left;
     }
     input{
       float: left;
       outline: #007AFF;
       .px2rem(height, 98);
       color:#8E8E93;
        .px2rem(width, 300);
        border:none;
     }
     i{
       float:right;
        .px2rem(margin-right, 30);
        font-style:normal;
        color:#007AFF;
     }
  }
  li:last-child{
    border:0;
  }
}
.user5_img{
   .px2rem(width, 80);
    .px2rem(height, 80);
    .px2rem(margin-top, 10);
    .px2rem(margin-right, 30);
    float:right;
}
@keyframes rotating{
from{transform:rotate(0)}
to{transform:rotate(360deg)}
}


.xz{
	.px2rem(width, 50);
    .px2rem(height, 50);
     .px2rem(left, 110);
      .px2rem(top, 110);
	border-radius: 50%;
	position: absolute;
	z-index: 99999999;
	border:5px solid green;
	border-left-color:#D8D8D8;
	animation:rotating 1.2s linear infinite
}
</style>



