<template>
  <div class="order">
     <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     订单明细
  </div>
      <!-- 切换 -->
    <div class="order_type">
       <div class="capital_box_nav">
       <span  v-bind:class="[type=='w00'?'activespan':'']" @click="change('w00')">未完成</span>
       <span  v-bind:class="[type=='w01'?'activespan':'']" @click="change('w01')">已完成</span>
     </div>
     <div class="order_type_title">
       <span>注：</span>用户完成一个记绩点即归为已完成，记绩点请查看激励政策
     </div>
    </div>
    <!--  未完成 -->
        <div v-if="type=='w00'">
          <div class="order_bank" v-for="(item,index) in no_wc" v-if="no_wc!=''">
            <h3>
            	<img src="../assets/icon_agentRewards.png" alt="" v-if="item.orderType=='00'">
           <img src="../assets/icon_cardRewards.png" alt="" v-if="item.orderType=='01'">
           <img src="../assets/icon_bonus.png" alt="" v-if="item.orderType=='02'">
            <img src="../assets/icon_rewards.png" alt="" v-if="item.orderType=='03'">
            {{item.orderName}}</h3>
            <div class="order_content">
              <div><span>客户姓名：</span>{{item.custNikeName}} （{{item.custId}}）</div>
              <div><span> 手机号：</span>{{item.custMobile}}</div>
              <div><span>申请时间：</span>{{item.orderCreateTime}}</div>
              <div><span>当时职级：</span>{{item.custLevelName}}</div>
              <div><span>订单状态：</span>{{item.orderStatusName}}</div>
            </div>
             <h4> <span v-if="item.isSearch==false" @click="ask_progress">进度查询</span><span @click="change_policy(index)">激励政策
               <div class="pub_tss" v-if="type=='w00'&&policy==index">
                 <ul>
                   <li v-for="(list,index) in item.tWcOrgComputing">
                     <p class="p1"><span>{{index+1}}</span>{{list.orgType}}</p>
                     <p class="p2">{{list.orgDesc}}</p>
                     <p class="p3">{{list.agentGainValue}}</p>
                   </li>
                 </ul>
               
               </div></span> </h4>
          </div>
           <div  class="tx_nodata"   v-if="no_wc==''">暂无订单信息</div>
        </div>
          <!--  已完成 -->
        <div v-else>
          <div class="order_bank" v-for="(itm,index) in ok_wc" v-if="ok_wc!=''">
            <h3>
         	<img src="../assets/icon_agentRewards.png" alt="" v-if="itm.orderType=='00'">
           <img src="../assets/icon_cardRewards.png" alt="" v-if="itm.orderType=='01'">
           <img src="../assets/icon_bonus.png" alt="" v-if="itm.orderType=='02'">
            <img src="../assets/icon_rewards.png" alt="" v-if="itm.orderType=='03'">
            {{itm.orderName}}<span>{{itm.allAgentGainValue}}</span></h3>
            <div class="order_content">
              <div><span>客户姓名：</span>{{itm.custNikeName}} （{{itm.custId}}）</div>
              <div><span> 手机号：</span>{{itm.custMobile}}</div>
              <div><span>申请时间：</span>{{itm.orderCreateTime}}</div>
              <div><span>当时职级：</span>{{itm.custLevelName}}</div>
              <div><span>订单状态：</span>{{itm.orderStatusName}}</div>
            </div>
             <h4> <span @click="change_policy(index)">激励政策
               <div class="pub_tss" v-if="type=='w01'&&policy==index" >
                 <ul>
                   <li v-for="(list,index) in itm.tWcOrgComputing">
                     <p class="p1"><span>{{index+1}}</span>{{list.orgType}}</p>
                     <p class="p2">{{list.orgDesc}}</p>
                     <p class="p3">{{list.agentGainValue}}</p>
                   </li>
                 </ul>
               </div></span> </h4>
          </div>
           <div  class="tx_nodata"   v-if="ok_wc==''">暂无订单信息</div>
        </div>
  </div>
</template>

<script>
import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'order',
  data () {
    return {
     type:'w00',
     policy:"-1",
     no_wc:"",
     ok_wc:"",
      flag:true,
      currentPage1:"0",
      currentPage2:"0",
      flag1:false,
       flag2:false,
    }
  },
    created(){
  	//未完成
	  	get("/wc_app/getCustOrderList?isFinish=W00&pageNo=0&pageSize=50&isApp=true").then(res=>{
	  console.log(res.data.status)
    		if(res.data.status==200){
    			if(res.data.data!=null){
    				 this.no_wc=res.data.data.orderList;
    			}
    			
    			 		
    		}else{
    			
    		}
	}).catch((res)=>{
		
	})
	//下拉加载更多
//	  	document.addEventListener('scroll', this.menu)
	},
  methods:{
   change:function(x){
   	this.policy="-1";
this.type=x;
//已完成
if(this.type=='w01'&&this.ok_wc==""){
	 	get("/wc_app/getCustOrderList?isFinish=W01&pageNo=0&pageSize=50&isApp=true").then(res=>{
    		if(res.data.status==200){
    			if(res.data.data!=null){
    				 this.ok_wc=res.data.data.orderList;
    			}
    			
    			 		
    		}else{
    			
    		}
	}).catch((res)=>{
		
	})
}
	 
   },
     change_policy:function(x){
     	if(x==this.policy){
     		this.policy="-1";
     	}else{
     		this.policy=x;
     	}
    
  },
  //进度查询
  ask_progress:function(x){
  	 this.$router.push({name:'progres',query:{type:x}});
  },
    //下拉加载更多
   	 menu(){    
 	var that=this;	
 	      	 var doe = document.documentElement || document.body,
                scrollHeight = doe.scrollHeight,
                clientHeight = doe.clientHeight,
                scrollY = window.scrollY || doe.scrollTop,
                offset = scrollHeight - scrollY - clientHeight;
                if(offset<300 ){
                	var datas;
                	if(that.flag){
                		
                		var currentPage;
                		if(that.type=="w00"){
                			if(that.flag1){
                				return false;
                			}
                				that.currentPage1++;
                				currentPage=that.currentPage1;
                		}else{
                			if(that.flag2){
                				return false;
                			}
                			that.currentPage2++;
                			currentPage=that.currentPage2;
                		}
 		        that.flag=false;
                var datas={"isFinish":that.type,"pageNo":currentPage,"pageSize":"10","isApp":"true"}
	 	get("/wc_app/getCustOrderList",datas).then( res=>{		
			if(res.data.data.orderList!=""){
				if(that.type=="w00"){
					if(res.data.data.orderList.length<11){
						that.flag1=true;
					}
					that.no_wc=that.no_wc.concat(res.data.data.orderList)
				}else{
					if(res.data.data.orderList.length<11){
						taht.flag2=true;
					}
					that.ok_wc=that.ok_wc.concat(res.data.data.orderList);
				}
			}else{
				that.flag=false;  
			}
			that.flag=true;
	})
                }}
   	 }
  }

}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.order{
  width: 100%;
  height:auto;
  overflow: hidden;
}
.order_type{
  width:100%;
  height:auto;
  background: #ffffff;
  overflow: hidden;
}
.capital_box_nav{
  .px2rem(width, 460);
    .px2rem(height, 54);
    margin:0 auto;
    border-radius: 8px;
    box-sizing: border-box;
    border:1px solid #007AFF;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .px2rem(margin-top,50);
    .px2rem(margin-bottom,50);
    overflow: hidden;
    span{
      width:50%;
      height:100%;
      display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .px2rem(font-size, 26);
    color:#007AFF;
    }
    .activespan{
      color:#ffffff;
      background-color:#007AFF;
    }
  }
  .order_type_title{
    width:100%;
    .px2rem(height, 54);
    .px2rem(font-size, 24);
    color:#000000;
    text-align: center;
    span{
      color:#FF8D00;
    }
  }
  .order_bank{
    background:#ffffff;
    height:auto;
    .px2rem(margin-bottom,20);
    h3{
      position: relative;
      width:100%;
      .px2rem(height, 80);
      .px2rem(line-height, 80);
      display: flex;
      flex-direction: row;
      align-items: center;
      border-top:1px solid #d1d1d6;
       border-bottom:1px solid #d1d1d6;
       font-family: 'PingFangSC-Medium';
       .px2rem(font-size,32);
      img{
        .px2rem(width, 40);
        .px2rem(height, 40);
        border-radius: 100%;
        .px2rem(margin-left, 40);
        .px2rem(margin-right, 20);
      }
      span{
        color:#F79200;
        position: absolute;
        right: 42px;
        height:100%;
        bottom:0;
      }
    }
      h4{
        position: relative;
     .px2rem(padding-right, 42);
      .px2rem(height, 80);
      .px2rem(line-height, 80);
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-end;
      border-top:1px solid #d1d1d6;
       border-bottom:1px solid #d1d1d6;
      .px2rem(font-size,26);
     color:#2E2E2E;
     span{
       display: block;
       .px2rem(width, 150);
       .px2rem(height, 54);
       .px2rem(line-height, 54);
       text-align: center;
     .px2rem(border-radius, 100);
     border:1px solid #d1d1d6;
     .px2rem(margin-left, 20);
     }
    }
    .order_content{
      width:100%;
      height:auto;
      overflow: hidden;
      background:#FAFAFA ;
       .px2rem(padding-top, 30);
       .px2rem(padding-bottom, 30);
      div{
        .px2rem(padding-left, 60);
        .px2rem(font-size, 28);
         .px2rem(line-height, 48);
          color:#2e2e2e;
 
        span{
                color:#8E8E93;
                .px2rem(width, 150);
                display: inline-block;
        }
      }
    }
  }
  .pub_tss{
    position: absolute;
    .px2rem(width, 630);
    height:auto;
    background:rgba(0, 0, 0, 0.8);
    .px2rem(bottom, 98);
    left:50%;
    transform: translateX(-50%);
    .px2rem(border-radius, 5);
   ul{
     height:auto;
     padding:20px;
     color:#ffffff;
     li{
     
       .px2rem(width, 520);
       .px2rem(margin-left, 42);
       display: flex;
       flex-direction: column;
       align-items: center;
         border-bottom:1px solid rgba(255,255,255,0.60);
         .px2rem(margin-bottom,20);
       p{
         width:100%;
         display: flex;
         .px2rem(line-height,40);
         
         flex-direction: row;
         align-items: center;
         .px2rem(font-size,28);
       }
       .p1{
         span{
           .px2rem(width, 28);
           .px2rem(height, 28);
            .px2rem(line-height, 28);
            text-align: center;
           border-radius: 100%;
           .px2rem(margin-left, -42);
           .px2rem(margin-right, 10);
         }
       }
       .p2{
          .px2rem(font-size,20);
       }
       .p3{
          color:#F79200;
       }
       
     }
     
   }
   ul:last-child{
         border:0;
       }
  }
    .pub_tss::after{
      position: absolute;
      content:'';
      width: 0; 
      height: 0; 
      border-left: 10px solid transparent; 
      border-right: 10px solid transparent; 
      border-top: 10px solid rgba(0, 0, 0, 0.8); 
      bottom:-10;
      .px2rem(right, 35)
    }
    .sr_nodata,.tx_nodata{
	width:100%;
	.px2rem(height,500);
	.px2rem(line-height,500);
	text-align: center;
	color:#8E8E93;
	.px2rem(font-size,30);
}
</style>



