<template>
  <div class="bankdetail">
     <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     资金明细
     <span class="pub_header_hb" @click="bc()">保存到相册</span>
     <span class="pub_ts" v-if='j_p'>请截屏保存到手机</span>
  </div>
    <img :src="bankdetail.orgDescUrl" alt="" class="img_bank"> 
    <div class="bankdetail_msg">
       <img :src="bankdetail.custImgUrl" alt="" class="bankdetail_msg_img1"> 
       <div class="bankdetail_msg_content">
         <span>{{bankdetail.custName}}</span>
         <span>职级：{{bankdetail.agentLevelName}}</span>
         <span>工号：{{bankdetail.custId}}</span>
       </div>
 <img :src="bankdetail.custQrCodeUrl" alt="" class="bankdetail_msg_img2"> 
    </div>

     <div class="foot_title">众包</div>
     <div class="foot_description">人人都可以挣银行的钱</div>
     <div class="foot_company">copyright@欣禾量澄 2018 v0.12</div>
  </div>
</template>

<script>
 import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'bankdetail',
  data () {
    return {
      msg: this.$route.query.type,
      bankdetail:"",
      j_p:false
    }
  },
  created(){
   	get("/wc_app/getWcProductInfo",{"orgId":this.msg}).then(res=>{
   		console.log(res)
    		if(res.data.status==200){
    			 this.bankdetail=res.data.data;
    		}else{
    			
    		}
	}).catch((res)=>{
		
	})
  },
  methods:{
    bc:function(){
  	this.j_p=!this.j_p;
  }
  }
}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.bankdetail{
width:100vw;
  margin:0 auto;
  height:100%;
}
.img_bank{
  width:100%;
  .px2rem(height, 800);
  margin:0;
  padding:0;
  display: block;
}
.bankdetail_msg{
  margin:0;padding:0;
  width:100%;
  .px2rem(height, 230);
   display: flex;
   flex-direction: row;
   align-items: center;
   background:#ffffff;
   .bankdetail_msg_img1{
     .px2rem(width, 120);
     .px2rem(height, 120);
     border-radius: 100%;
     .px2rem(margin-left, 40);
       .px2rem(margin-right, 18);
   }
     .bankdetail_msg_img2{
     .px2rem(width, 120);
     .px2rem(height, 120);
     
   }
   .bankdetail_msg_content{
     .px2rem(width, 410);
     height:100%;
     display: flex;
     flex-direction: column;
     justify-content: center;
     color:#666666;
     .px2rem(font-size,24);
     span:nth-of-type(1){
 color:#2E2E2E;
     .px2rem(font-size,32);
     }
   }
}
.foot_title{
  .px2rem(margin-top, 40);
}
</style>



