<template>
  <div  :class="[hb_flag?'capital team_a':'capital']">
   <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     资金明细
     <span class="pub_header_hb" id="pub_header_hb" @click="show_hb"><img src="../assets/icon_redPackage.png" class="hb" />¥ 166&times;3 红包</span>
  </div>
  <!--  -->
   <div class="capital_money">
     <div class="capital_moneys capital_moneysline" >
       <span>总收入</span>
        <span>¥ {{money.total}}</span>
     </div>
      <div class="capital_moneys">
       <span>可提现</span>
        <span>¥ {{money.usefulCash}}</span>
     </div>
   </div>
   <!-- 收入 -->
   <div class="capital_box_ball">
   <div class="capital_box">
     <div class="capital_box_nav">
       <span  v-bind:class="[type=='I'?'activespan':'']" @click="change('I')">收入</span>
       <span  v-bind:class="[type=='O'?'activespan':'']" @click="change('O')">提现</span>
     </div>
     <!-- 循环用户数据 收入 -->
     <div class="capital_box_msg" v-if="type=='I'">
       <div class="capital_box_msg_sr" v-for="item in sr" >
         <div class="capital_box_msg_sr1">
         	<img src="../assets/icon_cardRewards.png" alt=""  class="icon_img" v-if="item.title=='办理信用卡'"/>
         	<img src="../assets/icon_bonus.png" alt=""  class="icon_img" v-if="item.title=='办理贷款'"/>
         	<img src="../assets/icon_agentRewards.png" alt=""  class="icon_img" v-if="item.title=='邀请代理'"/>
         	<img src="../assets/icon_rewards.png" alt=""  class="icon_img" v-if="item.title=='红包奖励'"/>
           <span>{{item.title}}{{'·'+item.bizType}}</span>  
           <span>+{{item.amt}}</span>  
         </div>
         <div class="capital_box_msg_sr2">
           记绩状态：{{item.status}}<br>
           收入来源：{{item.source}}<br>
           当时职级：{{item.rank}}<br>
          办理时间：{{item.time}}<br>
           入账订单：{{item.orderId}}
         </div>
       </div>
       <div  class="sr_nodata"   v-if="sr.length==0">你还没有产生收入</div>
     </div>
     <!-- 提现 -->
       <div class="capital_box_msg" v-else>
       <div class="capital_box_msg_sr" v-for="data in tx">
         <div class="capital_box_msg_sr1">
         <img src="../assets/icon_withdraw.png" alt=""  class="icon_img" />
           <span>{{data.title}}</span>  
           <span id="capital_box_msg_sr1span">+{{data.amt}}</span>  
         </div>
         <div class="capital_box_msg_sr2">
           资金状态：{{data.status}}<br>
           办理时间： {{data.time}}<br>
           提现订单:  {{data.orderId}}
         </div>
       </div>
        <div  class="tx_nodata"   v-if="tx.length== 0">你没有提现记录</div>
     </div>
   </div>
   </div>
  <!-- 底部 -->
    <div class="foot_title">众分金融</div>
     <div class="foot_description">人人都可以挣银行的钱</div>
     <div class="foot_company" id="foot_company">copyright@欣禾量澄 2018 v0.12</div>
     <!--红包奖励-->
     <div class="mask" v-if="hb_flag">
     	<div class="hb_box">
     		<img src="../assets/close.png" class="hb_close" @click="show_hb" />
     		<h2>红包奖励</h2>
     		<p>奖励您的辛苦付出<br>根据业绩可以申请领取相应的红包</p>
     		<div class="hb_box_div" @click="attain('04')">
     			<img src="../assets/icon_redPackage.png" alt="" class="hb_logo"/>
     			<span class="span1">¥ 166 红包一个</span>
     			<span class="span2">累计直推 15 张信用卡/贷款，</span>
     			<span class="span3">或者累计直拓 10 名下级代理，可申请</span>
     			<img src="../assets/gray_arrow.png" alt=""  class="hb_close"/>
     		</div>
     		<div class="hb_box_div"  @click="attain('05')">
     			<img src="../assets/icon_redPackage.png" alt="" class="hb_logo"/>
     			<span class="span1">¥ 166 红包两个</span>
     			<span class="span2">只要成为金融家即可申请</span>
     			<img src="../assets/gray_arrow.png" alt=""  class="hb_close"/>
     		</div>
     		
     	</div>
     </div>
 <!-- 提现按钮 -->
  <div class="widthdraw_tx" @click="to_tx">提现</div>
    <toast  :msg=msg ref='toast' ></toast>
  </div>
</template>

<script>
	import toast from './toast.vue'
 import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'capital',
   components:{toast},
  data () {
    return {
      msg: '',
      type:'I',
      money:'',
      sr:[],
      tx:"",
      hb_flag:false,
      flag:true,
      currentPage1:"0",
      currentPage2:"0",
      hb_money:''
    }
  },
  created(){
  	//获取用户可提现金额
	  	get("/wc_app/query_cash_total").then(res=>{
    		if(res.data.status==200){
    			 this.money=res.data.data;
    		}else{
    			this.$refs.toast.sign(res.data.msg);
    		}
	}).catch((res)=>{
		
	})
	//获取收入列表
	  	get("/wc_app/query_cash_detail?type=I&currentPage=0&perPageNum=50").then(res=>{
    		if(res.data.status==200){
    			 this.sr=res.data.data.list;
    		}else{
    			this.$refs.toast.sign(res.data.msg);
    		}
	}).catch((res)=>{
		console.log(res)
	})
	//下拉加载更多
//	  	document.addEventListener('scroll', this.menu)
  },
  methods:{
   change:function(x){
     this.type=x;
     if(x=='O'&&this.tx==""){
     	//获取收入列表
	  	get("/wc_app/query_cash_detail?type=O&currentPage=0&perPageNum=50").then(res=>{
    		if(res.data.status==200){
    			 this.tx=res.data.data.list;
    		}else{
    			this.$refs.toast.sign(res.data.msg);
    		}
	}).catch((res)=>{
		console.log(res)
	})
     }
    
   },
   to_tx:function(){
   	this.$router.push({path:'/withdrawals'});
   },
   show_hb:function(){
   	this.hb_flag=!this.hb_flag;
   },
   attain:function(x){
   	//红包
	  	get("/wc_app/get_red_package",{"redPackageType":x}).then(res=>{
	  		console.log(res)
    		if(res.data.status==200){
    			this.$refs.toast.sign(res.data.msg);
    		}else{
    			this.$refs.toast.sign(res.data.msg);
    		}
	}).catch((res)=>{
		
	})
   },
   //下拉加载更多
   	 menu(){    
 	var that=this;	
 	      	 var doe = document.documentElement || document.body,
                scrollHeight = doe.scrollHeight,
                clientHeight = doe.clientHeight,
                scrollY = window.scrollY || doe.scrollTop,
                offset = scrollHeight - scrollY - clientHeight;
                if(offset<300 ){
                	var datas;
                	if(that.flag){
                		var currentPage;
                		if(that.type=="I"){
                				that.currentPage1++;
                				currentPage=that.currentPage1;
                		}else{
                			that.currentPage2++;
                			currentPage=that.currentPage2;
                		}
 		        that.flag=false;
                var datas={"type":that.type,"currentPage":currentPage,"perPageNum":"10"}
	 	get("/wc_app/query_cash_detail",datas).then( res=>{		
			if(res.data.data.list!=""){
				if(that.type=="I"){
					that.sr=that.sr.concat(res.data.data.list)
				}else{
					that.tx=that.sr.concat(res.data.data.list)
				}
			}else{
				
				that.flag=false;  
			}
			that.flag=true;
	})
                }}
   	 }
  }
}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.capital{
  width:100%;
  height: auto;
  overflow-x: hidden;
  margin:0 auto;
}
.capital_money{
  width:100%;
  .px2rem(height, 120);
  .capital_moneys{
    position: relative;
    width:50%;
    height:100%;
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    span:nth-of-type(1){
      .px2rem(font-size, 28);
      color:#2e2e2e;
      .px2rem(margin-left, 50)
    }
      span:nth-of-type(2){
      .px2rem(font-size, 36);
      color:#2e2e2e;
      .px2rem(margin-left, 50);
      font-family: "PingFangSC-Medium";
    }
   
  }
  .capital_moneysline::after{
    content:'';
      position: absolute;
      .px2rem(width, 1);
       .px2rem(height, 80);
       border-radius: 100%;
       background-color:#d1d1d6;
       .px2rem(right, 0);
        .px2rem(top, 20);

  }
  
}
.capital_box{
  width:100%;
  height:auto;
  overflow: hidden;
  background-color:#ffffff;
  .capital_box_nav{
  	overflow: hidden;
  .px2rem(width, 460);
    .px2rem(height, 54);
    margin:0 auto;
    border-radius: 8px;
    box-sizing: border-box;
    border:1px solid #007AFF;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .px2rem(margin-top,50);
    .px2rem(margin-bottom,50);
    overflow: hidden;
    span{
      width:50%;
      height:100%;
      display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .px2rem(font-size, 26);
    color:#007AFF;
    }
    .activespan{
      color:#ffffff;
      background-color:#007AFF;
    }
  }
}
.capital_box_msg{
  width:100%;
  height:auto;
  border-top:1px solid #d1d1d6;
  border-bottom:1px solid #d1d1d6;
}
.capital_box_msg_sr{
  .px2rem(width, 650);
  height:auto;
  .px2rem(padding-top, 40);
   .px2rem(padding-bottom, 40);
   .px2rem(margin-left, 100);
   .px2rem(font-size, 24);
   color:#8E8E93;
    border-bottom:1px solid #d1d1d6;
   .capital_box_msg_sr1{
     .px2rem(height,45);
     width:100%;
     color:#2E2E2E;
     .px2rem(font-size, 32);
    position: relative;
    
    :first-child{
      float:left;
    }
    :last-child{
      float:right;
     .px2rem( margin-right, 50);
     .px2rem(font-size, 36);
     color:#F79200;
    }
    #capital_box_msg_sr1span{
        color:#2E2E2E;
    }
   }
     .capital_box_msg_sr1 .icon_img{
      position: absolute;
      .px2rem(width, 40);
       .px2rem(height, 40);
       border-radius: 100%;
       .px2rem(left, -60);
        .px2rem(top, 2);
    }
}
.capital_box_ball{
  width:100%;
  .px2rem(min-height, 720);

}
.capital_box_msg_sr:last-child{
  border:none;
}
.widthdraw_tx{
  width:100%;
  .px2rem(height, 120);
  .px2rem(line-height, 120);
  text-align: center;
  background-color:#007AFF;
  position: fixed;
  z-index: 99;
  bottom:0;
  left:0;
  color:#ffffff;
  .px2rem(font-size, 34)
}
.foot_company{
  .px2rem(padding-bottom, 150);
}
.sr_nodata,.tx_nodata{
	width:100%;
	.px2rem(font-size,30);
	.px2rem(height,500);
	.px2rem(line-height,500);
	text-align: center;
	color:#8E8E93;
}
#pub_header_hb{
	background:#ffffff;
	color:#FF532A;
	right:0;
	top:50%;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	transform: translateY(-50%);
	.px2rem(width,249);
	.px2rem(height,60);
	border-bottom-left-radius: 30px;
	border-top-left-radius: 30px;
}
#pub_header_hb>.hb{
	display: block;
	position: static;
	.px2rem(width,34);
	.px2rem(height,42);
	.px2rem(margin-right,10);
}
.hb_box{
	.px2rem(width,670);
	height:auto;
	overflow: hidden;
	position: absolute;
	top:50%;
	left:50%;
	transform: translate(-50%,-50%);
	background:#ffffff;
	border-radius:10px;
	
	h2{
		width:100%;
		text-align: center;
		.px2rem(font-size,40);
		.px2rem(padding-top,60);
		.px2rem(padding-bottom,54);
		
	}
	p{
		width:100%;
		color:#8E8E93;
		text-align: center;
		text-align: center;
		.px2rem(font-size,30);
		.px2rem(padding-bottom,54);
		}
		.hb_box_div{
			border-top:1px solid #D1D1D6;
			.px2rem(padding-left,106);
			.px2rem(padding-top,30);
			.px2rem(height,170);
			display: flex;
			flex-direction: column;
			position: relative;
			color:#FF532A;
			.px2rem(font-size,24);
			.span1{
		.px2rem(font-size,36);
		.px2rem(line-height,60);
		font-family: "PingFangSC-Medium";
			}
			.hb_logo{
				position: absolute;
				.px2rem(top,30);
				.px2rem(left,30);
				.px2rem(width,50);
	            .px2rem(height,62);
			}
			.hb_close{
				position: absolute;
				top:50%;
				right:20px;
				transform: translateY(-50%);
				.px2rem(width,18);
	            .px2rem(height,32);
			}
		}
}
.team_a{
	position: fixed;
}
.hb_close{
	position: absolute;
	right:15px;
	top:15px;
	.px2rem(width,55);
	.px2rem(height,55);
}
#toast{
	width:0;
	height:0;
	overflow: hidden;
}
</style>



