<template>
  <div class="recharge">
      <div class="pub_header">
     获取代理权
  </div>
   <div class="recharge_1">请缴纳 166 元平台使用费获得代理权</div>
   <div class="recharge_2">（累计直推15张信用卡后，您可获得166元红包奖励）</div>
    <div class="recharge_2">（成为金融家，可再获得166×2的红包奖励）</div>
    <div class="recharge_3">有问题，找专属客服</div>
     <div class="recharge_4">
       <img :src="user.picUrl" class="header_img">
       <div class="recharge_4_center">
         <span>{{user.name}}</span>
          <span>工号：{{user.custId}} &nbsp;职称：{{user.rank}}</span>
          </div>
          <img src="../assets/wxlogo.png"  class="wx" @click="wxtype(user.qrUrl)"/>
         <a :href="'tel:'+user.mobile" class="call"></a>
     </div>
     <div class="recharge_5"></div>
       <div class="recharge_6">支付：代理授权费</div>
       <div class="recharge_7">¥166.00</div>
         <div class="recharge_8">
           <img src="../assets/alipay.png" alt="" class="alipay">
           支付宝
               <img src="../assets/ok.png" alt="" class="ok">
         </div>
        <div class="btn" @click="zf">立即支付</div>
        <!--展示二维码-->
        <div class="mask" v-if="wx">
        	<div class="qr">
        		<div class="qr1">请用微信扫描<br>专属客服的二维码</div>
        		<img :src="src_img" alt="" class="qr_img"/>
        		<div class="qr_bc">
        			请截屏保存到相册
        		</div>
        		<img src="../assets/close.png" alt="" class="close" @click="wx=false" />
        	</div>
        </div>
        <div id="payDiv" ></div>
  </div>
</template>

<script>
	 import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'recharge',
  data () {
    return {
      msg: '',
      wx:false,
      user:" ",
      src_img:'',
      flag:true
    }
  },
   created(){
    	get("/wc_app/query_up_cust_info").then(res=>{
    		if(res.data.status==200){
    			 this.user=res.data.data;
    		}
		
	})
  },
  methods:{
    wxtype:function(x){
     this.wx=true;
     this.src_img=x;
    },
    zf:function(){
    	if(this.flag){
    		this.flag=false;
    		    var payDiv=document.getElementById("payDiv");
    	 	get("/wc_app/payOrder").then(data=>{
    	 		console.log(data.data.code)
    		  if(data.data.code=="0000"){
    		  	const div = document.createElement('div');
    div.innerHTML = data.data.data;
    document.body.appendChild(div);
   document.forms['punchout_form'].submit();
            }else{
              alert(data.data.msg);
            }
		this.flag=true;
	})
    	}

    }
  }
}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.recharge{
  width: 100vw;
  height:100vh;
  overflow-y: auto;
  background:#ffffff;
}
.recharge_1{
  font-family: 'PingFangSC-Medium';
  color:#2E2E2E;
  .px2rem(font-size, 40);
  width:100%;
  text-align: center;
  .px2rem(padding-top, 120);
}
.recharge_2{
  color:#FF532A ;
  .px2rem(font-size, 26);
  width:100%;
  text-align: center;
}
.recharge_3{
  color:#8E8E93;
  .px2rem(font-size, 24);
  width:100%;
    .px2rem(padding-left, 40);
      .px2rem(padding-top, 72);
            .px2rem(padding-bottom, 10);
}
.recharge_4{
  width:100%;
  .px2rem(height, 120);
    border-top:1px solid #d1d1d6;
      border-bottom:1px solid #d1d1d6;
      display: flex;
      flex-direction: row;
      align-items: center;
  .header_img{
    .px2rem(width, 80);
     .px2rem(height, 80);
    .px2rem(margin-left, 40);
     .px2rem(margin-right,8);
  }
  .recharge_4_center{
    .px2rem(width, 424);
    height:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color:#8E8E93;
    :first-child{
       .px2rem(font-size, 28);
    }
    :last-child{
       .px2rem(font-size, 24);
    }
  }
}
.wx{
   .px2rem(width, 60);
     .px2rem(height, 60);
     border-radius: 100%;
   .px2rem(margin-right,50);
}
.call{
  background: url(../assets/call.png) no-repeat;
  background-size: cover;
  .px2rem(width, 60);
     .px2rem(height, 60);
}
.recharge_5{
  width:100%;
  .px2rem(height,20);
  background-color:#F6F5F5;
}
.recharge_6{
  color:#2E2E2E;
  .px2rem(font-size, 40);
  width:100%;
  text-align: center;
  .px2rem(padding-top, 56);
    border-top:1px solid #d1d1d6;
}
.recharge_7{
  color:#FF532A;
  .px2rem(font-size, 40);
  width:100%;
  text-align: center;
  .px2rem(padding-bottom, 46);
}
.recharge_8{
   width:100%;
  .px2rem(height, 100);
    border-top:1px solid #d1d1d6;
      border-bottom:1px solid #d1d1d6;
      display: flex;
      flex-direction: row;
      align-items: center;
     color:#2E2E2E;
     .px2rem(font-size, 32);
      .alipay{
        .px2rem(width, 60);
     .px2rem(height, 60);
      .px2rem(margin-left,40);
       .px2rem(margin-right,18);
      }
      .ok{
           .px2rem(width, 50);
     .px2rem(height, 50);
      .px2rem(margin-left,453);
      }
}
.qr{
	position: fixed;
	left:50%;
	top:50%;
	transform: translate(-50%,-50%);
	.px2rem(width,670);
	.px2rem(height,1028);
	background-color:#ffffff;
	border-radius: 20px;
	overflow: hidden;
	.qr1{
		width:100%;
		text-align: center;
		.px2rem(padding-top,86);
		.px2rem(padding-bottom,48);
		color:#000000;
		.px2rem(font-size,40);
	}
	.qr_img{
		.px2rem(height,530);
		.px2rem(width,530);
		margin:0 auto;
		display: block;;
	}
	.qr_bc{
		position: absolute;
		bottom:0;
		left:0;
		width:100%;
		.px2rem(height,100);
		.px2rem(line-height,100);
		text-align: center;
		background-color:#007AFF;
		 color:#ffffff;
     .px2rem(font-size, 34);
	}
}
.close{
	position: absolute;
	right:20px;
	top:20px;
}
</style>



