  created(){ 
  	// 获取用户金额信息

get("/wc_app/query_cash_total").then((res)=>{
	this.money=res.data.data;
   		
   	})
//获取用户信息
get("/wc_app/query_cust_info").then((res)=>{
	this.user=res.data.data;
	if(res.data.data.qrUrl==null){
		this.msg=true;
	}
   		
   	})
},
  methods:{
 to_invitedqr:function(x){
 	this.$router.push({name:'invitedqr',query:{type:x}});
 }
  },
