<template>
  <div class="banklist">
    <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     激励政策
  </div>
    <div class="banklist_box">
   <div class="banklist_nav">
     <span v-bind:class="[type=='BANK'?'active':'']" @click="changetype('BANK')">邀请办卡</span>
      <span v-bind:class="[type=='LOANCOM'?'active':'']" @click="changetype('LOANCOM')">邀请贷款</span>
       <span v-bind:class="[type==3?'active':'']" @click="changetype('3')">邀请代理</span>
    </div>
    <!-- type1 -->
    <div class="banklist_list_box" v-if='type=="BANK"'>
        <div class="bank_list_pub" @click="bankdetail(item.orgId)" v-for="item in bank">
          <div class="bank_list_pub_left">
            <img :src="item.orgImgUrl" alt="">
           {{item.orgName}}
          </div>
            <div class="bank_list_pub_right">
            <p class="p1"><i>奖励金：</i><span>{{item.bounty}}</span></p>
             <p class="p1"><i>通过率：</i><span>{{item.orgThroughputRate}}</span></p>
              <p class="p2"><i>奖励金：</i><span>{{item.rewardPoint}}</span></p>
              <img src="../assets/gray_arrow.png" alt="">
          </div>
        </div>
    </div>
      <!-- type2 -->
    <div class="banklist_list_box" v-if='type=="LOANCOM"'>
        <div class="bank_list_pub" v-for="itm in loancom" @click="bankdetail(itm.orgId)">
          <div class="bank_list_pub_left">
            <img  :src="itm.orgImgUrl"  alt="">
            {{itm.orgName}}
          </div>
            <div class="bank_list_pub_right">
            <p class="p1"><i>奖励金：</i><span>{{itm.bounty}}</span></p>
             <p class="p1"><i>通过率：</i><span>{{itm.orgThroughputRate}}</span></p>
              <p class="p2"><i>最高额度：</i><span>{{itm.orgMaxAmount}}</span></p>
               <p class="p2"><i>平均额度：</i><span>{{itm.orgAvgAmount}}</span></p>
              <p class="p2"><i>奖励点：</i><span>{{itm.rewardPoint}}</span></p>
              <img src="../assets/gray_arrow.png" alt="">
          </div>
        </div>
    </div>
      <!-- type3 -->
    <div class="banklist_list_box2" v-if='type==3'>
        <promote></promote>
    </div>
    </div>
   <!-- 底部 -->
     <div class="foot_title">众分金融</div>
     <div class="foot_description">人人都可以挣银行的钱</div>
     <div class="foot_company">copyright@欣禾量澄 2018 v0.12</div> 
    
  </div>
</template>

<script>
import promote from './promote_public.vue'
 import {get}  from '../utils'
import Vue from 'vue'
export default {
   components: { promote},
  name: 'banklist',
  data () {
    return {
      type:'BANK',
      bank:[],
      loancom:"",
        flag:true,
      currentPage1:"0",
      currentPage2:"0"
    }
  },
  created(){
    	//邀请办卡
	  	get("/wc_app/getWcProductList",{"orgType":"BANK"}).then(res=>{
    		if(res.data.status==200){
    			 this.bank=res.data.data.BANK;
    		}else{
    			
    		}
	}).catch((res)=>{
		
	})
	//下拉加载
	//下拉加载更多
	  	document.addEventListener('scroll', this.menu)
  },
  methods:{
    changetype:function(x){
     this.type=x;
     if(this.loancom==''&&this.type=="LOANCOM"){
     	  	get("/wc_app/getWcProductList",{"orgType":"LOANCOM"}).then(res=>{
    		if(res.data.status==200){
    			 this.loancom=res.data.data.LOANCOM;
    			
    		}else{
    			
    		}
	}).catch((res)=>{
		
	})
     }
    },
    bankdetail:function(x){
         this.$router.push({name:'bankdetail',query:{type:x}});
    },
     //下拉加载更多
   	 menu(){    
 	var that=this;	
 	      	 var doe = document.documentElement || document.body,
                scrollHeight = doe.scrollHeight,
                clientHeight = doe.clientHeight,
                scrollY = window.scrollY || doe.scrollTop,
                offset = scrollHeight - scrollY - clientHeight;
                if(offset<300 ){
                	var datas;
                	if(that.flag){
                		var currentPage;
                		if(that.type=="BANK"){
                				that.currentPage1++;
                				currentPage=that.currentPage1;
                		}else{
                			that.currentPage2++;
                			currentPage=that.currentPage2;
                		}
 		        that.flag=false;
                var datas={"type":that.type,"pageNo":currentPage}
	 	get("/wc_app/getWcProductList",datas).then( res=>{		
			if(res.data.data.BANK!=""){
				if(that.type=="BANK"){
					that.bank=that.bank.concat(res.data.data.BANK)
				}else{
					that.loancom=that.loancom.concat(res.data.data.LOANCOM);
					console.log(that.loancom)
				}
			}else{
				that.flag=false;  
			}
			that.flag=true;
	})
                }}
   	 }
  }
}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.banklist{
width:100vw;
  margin:0 auto;
}
.banklist_box{
  width:100%;
  .px2rem(min-height, 890);
  background-color:#ffffff;
  .px2rem(padding-top, 60);
  .px2rem(padding-bottom, 40);
}
.banklist_nav{
 .px2rem(width,690);
 margin:0 auto;
  .px2rem(height,56);
   box-sizing: border-box;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border:1px solid #007AFF;
  .px2rem(border-radius,8);
  span{
   .px2rem(width, 230);
    .px2rem(line-height, 56);
    text-align: center;
    .px2rem(font-size, 26);
    color:#2E2E2E;
    box-sizing: border-box;
  }
  span:nth-of-type(2){
    border-left:1px solid #007AFF;
    border-right:1px solid #007AFF;
  }
}
.banklist_nav .active{
  color:#ffffff;
  background-color:#007AFF;
}
// 循环列表公共样式
.bank_list_pub{
  margin: 0 auto;
  .px2rem(width, 710);
  height: auto;
  overflow: hidden;
  box-sizing: border-box;
  border:1px solid #D1D1D6;
  .px2rem(border-radius,4);
  .px2rem(padding-top, 26);
  .px2rem(padding-bottom, 26);
  background:#FAFAFA;
  .bank_list_pub_left{
    float: left;
    .px2rem(width, 194);
    height:auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color:#2E2E2E ;
    
    .px2rem(font-size,28);
    img{
       .px2rem(width, 72);
  .px2rem(height, 72);
  .px2rem(margin-bottom, 14);
border-radius: 100%;
    }
  }
  .bank_list_pub_right{
     border-left:1px solid #D1D1D6;
    height:100%;
    .px2rem(width,500);
     display: flex;
    flex-direction: column;
    justify-content: center;
float: left;
position: relative;
img{
  position: absolute;
  right:0;
  top:50%;
  .px2rem(width,18);
	            .px2rem(height,32);
  transform: translateY(-50%);
}
p{
  color:#8E8E93;
  .px2rem(font-size, 24);
  .px2rem(line-height,43);
}
p>i{
  font-style: normal;
  .px2rem(width, 130);
  display: inline-block;
  text-align: right;
}
.p1>span{
  color:#FF3F00 ;
  font-family: 'PingFangSC-Semibold';
}
.p2>span{
color:#000000;
}
  }
}
.banklist_list_box{
    .px2rem(margin-top, 60);
}
</style>



