<template>
  <div id="toast">
   
    <span class="toast" v-if="msg!=''" >
      {{msg}}
    </span>
    <span>{{seconds>0?seconds:date}}{{date=='发送验证码'?'':' (s)'}}</span>
  </div>
</template>

<script>
import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'toast',
   data () {
    return {
      msg:'',
      date:"发送验证码"
    }
  },
  created(){

  },
   methods:{
      sign(msg) {
        this.msg = msg;
        var that=this;
        setTimeout(function(){
          that.msg='';
        },2500)
    },
    //获取验证码
    message(){
    	var that=this;
    	if(!(that.date>0)){
    		  get("/wc_app/get_code_no_mobile").then(res=>{
    		  	if(res.data.status==201||res.data.status==202){
    		  		 that.sign(res.data.msg);
    		  		 return false;
    		  	}
    		  		if(res.data.status==200){
    		  			that.date=res.data.msg;
    		 var interval=setInterval(function(){
    		 	
    		 	if(that.date<=0){
    		 		clearInterval(interval)
    		 		that.date="重新获取验证码";
    		 		console.log(that.date)
    		 	}else{
    		 		that.date--;
    		 	}	 		
  	 	},1000)
    	     that.sign("验证码发送成功"); 
    		  	}
    		
	}).catch((res)=>{
		console.log(res)
	})
    	}
    	
    }
   
  },
  computed: {
  	 seconds() {
  	 		 return this.date
      }
  }
}
</script>

<style lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
[ v-cloak]{
	display: none;
}
#toast{
	display: inline-block;
	.px2rem(width,200);
	.px2rem(height,20);
	text-align: right;
}
.toast{
  padding:10px 50px;
  .px2rem(min-width,350);
  text-align: center;
  background:rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  position: fixed;
  top:50%;
  left:50%;
  transform: translate(-50%,50%);
  z-index: 99999999;
  color:#ffffff;
  .px2rem(fomnt-size, 30);
}

</style>



