<template>
  <div class="extension">
   <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     推广素材
  </div>
  <div class="extension_nav">分享下述文章，可帮助获得客户，锁定粉丝</div>
  <div class="extension_content" @click="to_extensiondetail(item.url,item.title)"  v-for="item in list">
    <span></span>{{item.title}}
  </div>
  </div>
</template>

<script>
	 import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'HelloWorld',
  data () {
    return {
      list:[]
    }
  },
  created(){
  //获取收入列表
	  	get("/wc_app/query_extend_material_list?currentPage=0&perPageNum=10").then(res=>{
	  		console.log(res.data.data)
    		if(res.data.status==200){
    			 this.list=res.data.data;
    		}else{
    			
    		}
	}).catch((res)=>{
		console.log(res)
	})
  },
  methods:{
   to_extensiondetail:function(x,y){
   this.$router.push({name:'extensiondetail',query:{index:x,title:y}});


   }
  }
}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.extension{
  width:100vw;
  min-height:100vh;
  background:#ffffff;
  .px2rem(font-size,32);
  color:#2E2E2E;
}
.extension_nav{
    width:100%;
    .px2rem(height, 70);
     .px2rem(line-height, 70);
     background:#F0F0F0 ;
     text-align: center;
       .px2rem(font-size,26);
         color:#FF532A ;
}
.extension_content{
border-top:1px solid #d1d1d6;

.px2rem(padding-left, 70);
.px2rem(padding-right, 26);
.px2rem(padding-top, 30);
.px2rem(padding-bottom, 30);
position: relative;
span{
  display:block;
   .px2rem(width, 20);
    .px2rem(height, 20);
    border-radius: 50%;
    background-color:#D8D8D8;
    position: absolute;
    .px2rem(top,40);
    .px2rem(left,30);
}
}
.extension_content:nth-last-of-type(1){
border-bottom:1px solid #d1d1d6;
}
</style>



