<template>
  <div class="service">
   <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     专属客服
  </div>
     <img :src="user.picUrl" alt="" class="header_img">
     <div class="name">{{user.name}}</div>
     <div class="level"><span>职级：{{user.rank}} </span><span>&nbsp;工号：{{user.custId}}</span></div>
      <div class="line"></div>
     <div class="call">电话沟通<span>{{user.mobile}}</span> <a :href="'tel:'+user.mobile"></a></div>
     <div class="wx_title">
      微信联系<span>请用微信扫描下方二维码</span>
     </div>
     <img :src="user.qrUrl" alt="" class="qr">
  </div>
</template>

<script>
 import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'service',
  data () {
    return {
       user:"",
    }
  },
  created(){
    	get("/wc_app/query_up_cust_info").then(res=>{
    		if(res.data.status==200){
    			 this.user=res.data.data;
    			 this.mob='tel:'+res.data.data.mobile;
    		}
		
	})
  },
  methods:{
    a:function(){
     
    }
  }
}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.service{
  width:100vw;
  height:100vh;
  background-color:#ffffff;
  overflow: hidden;
}
.header_img{
  .px2rem(width, 160);
   .px2rem(height, 160);
   display: block;
   border-radius: 50%;
   margin:0 auto;
   .px2rem(margin-top, 42);
}
.name{
  .px2rem(font-size, 32);
  color:#2E2E2E ;
  .px2rem(margin-top, 18);
  .px2rem(margin-bottom, 18);
  text-align: center;
}
.level{
    .px2rem(font-size, 24);
  color:#666666;
  .px2rem(margin-top, 18);
  .px2rem(padding-bottom, 46);
  text-align: center;
}
.line{
  width:100%;
  .px2rem(height, 20);
  background:#F0F0F0;
}
.call{
  position: relative;
  .px2rem(height, 120);
  .px2rem(line-height, 120);
  border-bottom:1px solid #d1d1d6;
   .px2rem(font-size, 28);
  color:#2E2E2E;
  .px2rem(padding-left, 40);
  display: flex;
  flex-direction: row;
  align-items: center;
  span{
     .px2rem(font-size, 32);
 font-family: 'PingFangSC-Medium';
 .px2rem(padding-left, 32);
  }
  a{ 
    display: inline-block;
    background: url(../assets/call.png) no-repeat;
    background-size: cover;
    .px2rem(width, 60);
   .px2rem(height, 60);
position: absolute;
.px2rem(right, 28);
top:50%;
transform: translateY(-50%);
  }
}
.wx_title{
  position: relative;
   .px2rem(font-size, 28);
  color:#2E2E2E;
  .px2rem(padding-top, 40);
   .px2rem(padding-bottom,20);
  .px2rem(padding-left, 40);
  display: flex;
  flex-direction: row;
  align-items: center;
  span{
    .px2rem(font-size, 32);
 font-family: 'PingFangSC-Medium';
 .px2rem(padding-left, 32);
  }
}
.qr{
   .px2rem(width, 400);
   .px2rem(height, 400);
   display: block;
   margin:0 auto;

}
</style>



