<template>
  <div class="bankdetail" v-if="msg">
     <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     {{msg=='APPLY_CREDITCARD'?"邀请办卡":''}}{{msg=='APPLY_LOAN'?"邀请贷款":''}}{{msg=='EXPAND_AGENT'?"邀请代理":''}}
     <span class="pub_header_hb" @click="bc()">保存到相册</span>
     <span class="pub_ts" v-if='j_p'>请截屏保存到手机</span>
  </div>
    <img src="../assets/bk00b8ee.jpg" alt="" class="img_bank" v-if="msg=='APPLY_CREDITCARD'"> 
     <img src="../assets/dkc20404.jpg" alt="" class="img_bank" v-if="msg=='APPLY_LOAN'"> 
      <img src="../assets/dlf0000.jpg" alt="" class="img_bank" v-if="msg=='EXPAND_AGENT'">  
     <!--<img src="../assets/1.jpeg" alt="" class="img_qr1" v-if="msg=='APPLY_CREDITCARD'"> 
     <img src="../assets/1.jpeg" alt="" class="img_qr2" v-if="msg=='APPLY_LOAN'"> 
     <img src="../assets/1.jpeg" alt="" class="img_qr3" v-if="msg=='EXPAND_AGENT'"> -->
     	<div :class="msg">
     	<img :src="'http://www.gbtags.com/gb/qrcode?t='+pic_url+'&s=290'" alt=""   id="img" v-if="msg=='EXPAND_AGENT'||msg=='APPLY_LOAN'" />
     	<img :src="url_img" alt=""   id="img2"  v-if="msg=='APPLY_CREDITCARD'"/>
     	</div>
  </div>
</template>

<script>
import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'bankdetail',
  data () {
    return {
      msg: this.$route.query.type,
      j_p:false,
      pic_url:'',
      url_img:''
    }
  },
  created(){
     //获取代理数量和客户数量
     if(this.msg!='APPLY_CREDITCARD'){
     		get("/wc_app/get_qr_url",{"qrCodeType":this.msg}).then(res=>{
  	   		console.log(res)
    		if(res.data.status==200){
    			 this.pic_url=encodeURIComponent(res.data.data.url);
    			 console.log(this.pic_url)
    		}
	})
     }else{
     	this.url_img="https://api.thinkinfo.tech:8203/xhlc-front-app/wc_app/get_qr_url?wc_identification="+window.localStorage.getItem("key")+"&qrCodeType=APPLY_CREDITCARD"
     }
  	   
  },
  methods:{
  bc:function(){
  	this.j_p=!this.j_p;
  }
  },
}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.bankdetail{
  width:100vw;
  height:100vh;
  position: relative;
  overflow: hidden;
}
.img_bank{
  width:100%;
  margin:0;
  padding:0;
  display: block;
}
.APPLY_CREDITCARD{
  .px2rem(width,230);
  .px2rem(height,230);
  position: absolute;
  .px2rem(top,275);
  left:50%;
  transform: translateX(-50%);
  z-index: 99;
  border-radius: 10px;
  padding:10px;
  overflow: hidden;
}
.APPLY_LOAN{
  .px2rem(width,245);
  .px2rem(height,245);
  position: absolute;
  .px2rem(top,350);
  .px2rem(left,250);
  z-index: 99;
 overflow: hidden;
 border-radius: 10px;
}
.EXPAND_AGENT{
  .px2rem(width,230);
  .px2rem(height,230);
  position: absolute;
 top:48%;
  left:50%;
  transform: translateX(-50%);
  z-index: 99;
  border-radius: 10px;
}
#img{
.px2rem(width,300);
  .px2rem(height,300);
  position: relative;
  .px2rem(left,-35);
   .px2rem(top,-35);
}
#img2{
.px2rem(width,240);
  .px2rem(height,240);
  position: relative;
  .px2rem(left,-5);
   .px2rem(top,-5);
}
</style>



