<template>
  <div class="withdrawals">
  <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
    提现
  </div>
  <div class="line"></div>
   <div class="withdrawals1">
     <div class="withdrawals1_title">收款账户</div>
    <div class="withdrawals1_content"  @click="toaccount"><span v-if="bank.bankNo">{{bank.bankNo}}</span><span class="active" v-else>请填写您的收款账户</span><img src="../assets/gray_arrow.png" alt=""></div>
     <div class="withdrawals1_content"  ></div>
   
  
   </div>
      <div class="line"></div>  
     
       <div class="withdrawals1_title">
         提现金额
       </div>
        <div class="withdrawals2_content">
         <label for="">¥</label>
          <input type="number" name="" id="" v-model="num" placeholder="0">
          <span @click="all">全部</span>
       </div>
       <div class="withdrawals2_money">可提现金额：{{money}}元</div>
       <div class="btn" @click="tx">提现</div>
       <div class="withdrawals2_foot">提现偶尔会出现延迟，请耐心等待</div>
       <toast  :msg=msg ref='toast' class="b"></toast>
     </div>
  
</template>

<script>
import toast from './toast.vue'
 import {get}  from '../utils'
import Vue from 'vue'
export default {
  name: 'withdrawals',
 components:{toast},
  data () {
    return {
    	  msg: '',
     account:false,
     num:'',
     bank:'',
     money:0,
     flag:true,
    }
  },
  created(){
    	get("/wc_app/query_cash_bank_card").then(res=>{
    		if(res.data.status==200){
    			 this.bank=res.data.data;
    		}else{
    			this.$refs.toast.sign(res.data.msg);
    		}
	}).catch((res)=>{
		console.log(res)
	})
	//获取用户可提现金额
	  	get("/wc_app/query_cash_total").then(res=>{
    			 this.money=res.data.data.usefulCash;
    		
	}).catch((res)=>{
		console.log(res)
	})
  },
  methods:{
    toaccount:function(){
      this.$router.push({path:'/addaccount'});
    },
    all:function(){
    	this.num=this.money;
    },
    //提现
    tx:function(){
    var a=this.num==''?'0':parseFloat(this.num);
    var b=parseFloat(this.money);
    
    	if(a>b||a==0){
    		 this.$refs.toast.sign("提现金额输入有误");
    	   	return false;
    	}
    	 if(this.flag){
    	 	this.flag=false;
    	 	  	get("/wc_app/withdraw_cash",{"cashAmt":this.num}).then(res=>{  	 	  		
    		if(res.data.status==200){
    			this.$refs.toast.sign("提现申请成功");
    			this.money=b-a;
    		}else{
    			this.$refs.toast.sign(res.data.msg);
    		}
    		this.flag=true;
	}).catch((res)=>{
		console.log(res)
	})
	
    	 }
    }
  }
}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.withdrawals{
  width:100vw;
  height:100vh;
  background-color:#ffffff;
 
}
.line{
  width:100%;
  .px2rem(height, 20);
  background-color:#F0F0F0 ;
}
.withdrawals1{
  .px2rem(height, 180);
   border-top:1px solid #d1d1d6;
    border-bottom:1px solid #d1d1d6;

  .withdrawals1_content{
     .px2rem(height, 50);
    span{
  .px2rem(margin-left, 40);
  .px2rem(font-size,36);
  color:#2E2E2E ;
  display: block;
  float: left;
}
.active{
  color:#FF532A;
}
img{
   .px2rem(height, 18);
    .px2rem(height, 32);
      .px2rem(margin-top, 9);
      .px2rem(margin-right, 30);
    float:right;
}
}
}
  .withdrawals1_title{
  .px2rem(margin-left, 40);
    .px2rem(margin-top, 20);
  .px2rem(margin-bottom, 34);
  .px2rem(font-size,28);
  color:#8E8E93 ;
  }
  .withdrawals2_content{
.px2rem(height,100);
.px2rem(line-height,100);
    .px2rem(width, 710);
    border-bottom:1px solid #d1d1d6;
    .px2rem(margin-left, 40);
    .px2rem(padding-top, 54);
     .px2rem(padding-bottom, 32);
     overflow: hidden;
     .px2rem(font-size, 72);
     color:#2E2E2E ;
     label{
       
       float: left;
     }
     input{
       .px2rem(width, 500);
       color:#000000;
       float: left;
      .px2rem(margin-left, 10);
     }
     span{
       .px2rem(font-size, 28);
       color:#007AFF ;
         .px2rem(height,100);
       .px2rem(line-height,100);
       float: right;
       .px2rem(margin-right,40);
     }
  }
  .withdrawals2_money{
    .px2rem(height,70);
.px2rem(line-height,70);
 .px2rem(padding-left, 40);
  color:#8E8E93;
  .px2rem(font-size, 24);
   border-bottom:1px solid #d1d1d6;
  }
  .withdrawals2_foot{
    width:100%;
    text-align: center;
    color:#8E8E93;
    .px2rem(font-size, 24);
    .px2rem(padding-top, 10);
  }
  .btns{
  opacity: 0.6;
  }
  #toast{
  	width:0;height:0;
  	overflow: hidden;
  }
</style>



