<template>
  <div :class="[wx?'team team_a':'team']">
   <div class="pub_header">
   <a v-on:click="$router.go(-1)"><img src="../assets/white_arrow.png" alt=""></a>
     团队管理
  </div>
    <div class="capital_box">
     <div class="capital_box_nav">
       <span  v-bind:class="[type=='AGENT'?'activespan':'']" @click="change('AGENT')">代理({{num.agentCount}})</span>
       <span  v-bind:class="[type=='COMMON'?'activespan':'']" @click="change('COMMON')">客户({{num.commonCount}})</span>
     </div>
    </div>
    <div class="team_box" v-if="type=='AGENT'">
      <div class="team_box_ball" v-for="item in dl">
     <div class="team_box1">
       <img :src="item.picUrl" alt="" class="header_img">
       <div class="team_box1_c">
         <span class="span1">{{item.nickname}}</span>
          <span> 职级：{{item.rank}}</span>
         <span>工号：{{item.custId}}</span>
          <span>加入时间：{{item.joinTime}}</span>
       </div>
       <img src="../assets/wxlogo.png" alt="" class="wximg" @click="wxshow(item.wxQrUrl)">
      <a :href="'tel:'+item.mobile"> <img src="../assets/call.png" alt="" class="callimg wximg"></a>
     </div>
      <div class="team_box2">
      <div class="team_box2_1">
        <i>昨日业绩</i>
           <span>拓展代理{{item.yesterAgent}}人</span>
           <span>新增客户{{item.yesterCommon}}人</span>
            <span>完成订单{{item.finishCount}}份，新增订单{{item.addCount}}份</span>
      </div>
      <div class="team_box2_1">
          <i>总计业绩</i>
          <span>完下级代理{{item.totalAgent}}人，客户{{item.totalCommon}}人</span>
      </div>
     </div>
     </div>
     <!--没有代理-->
     <div class="no_dl" v-if="dl==null">
     	<span class="span_1">你还没有拓展代理</span>
     	<span class="span_2">赶紧去拓展代理吧，每拓展一个代理都有收入哦</span>
     	 <router-link to="/invitedqr?type=EXPAND_AGENT">我的拓展二维码</router-link>
   
     </div>
    </div>
     <div class="team_box" v-else>
        <div class="team_box_list" v-for="item in kf">
            <img :src="item.picUrl" alt="" class="header_img">
             <div class="team_box1_c">
         <span class="span1">{{item.nickname}}</span>
          <span> 职级：{{item.rank}}</span>
         <span>工号：{{item.custId}}</span>
          <span>加入时间：{{item.joinTime}}</span>
       </div>
        <a :href="'tel:'+item.mobile"> <img src="../assets/call.png" alt="" class="callimg wximg"></a>
        </div>
         <!--没有客户-->
     <div class="no_dl" v-if="kf==null">
     	<span class="span_1">你还没有客户</span>
     	<span class="span_2">客户才是真正为你支付薪水的人</span>
     	 <router-link to="/invitedqr?type=APPLY_CREDITCARD">我的获客二维码</router-link>
   
     </div>
     </div>
      <!--展示二维码-->
        <div class="mask" v-if="wx">
        	<div class="qr">
        		<div class="qr1">请用微信扫描<br>专属客服的二维码</div>
        		<img :src="wxQrUrl" alt="" class="qr_img"/>
        		<div class="qr_bc">
        			请截屏保存到手机
        		</div>
        		<img src="../assets/close.png" alt="" class="close" @click="wx=false" />
        	</div>
        </div>
  </div>
</template>

<script>
import {get}  from '../utils'
import Vue from 'vue'

export default {
  name: 'team',
  data () {
    return {
       type:'AGENT',
        dl:'',
        kf:'',
        currentPage:0,
        wx:false,
        num:'',
        wxQrUrl:"",
        flag:true,
      currentPage1:"0",
      currentPage2:"0"
    }
  },
  created(){
  	//获取代理数量和客户数量
  	   	get("/wc_app/query_team_num").then(res=>{
    		if(res.data.status==200){
    			 this.num=res.data.data;
    		}
	})
  //获取代理
    	get("/wc_app/query_team",{"currentPage":0,"perPageNum":"10","type":'AGENT'}).then(res=>{
    		if(res.data.status==200){
    			 this.dl=res.data.data.list;
    		}	
	})
    	//下拉加载更多
	  	document.addEventListener('scroll', this.menu)
  },
  methods:{
    change:function(x){
     this.type=x;
    if(this.type=='COMMON'&&this.kf==""){
    	  	get("/wc_app/query_team",{"currentPage":0,"perPageNum":"10","type":'COMMON'}).then(res=>{
    		if(res.data.status==200){
    			 this.kf=res.data.data.list;
    		}	
	})
    }
   },
   wxshow:function(x){
   	this.wxQrUrl=x;
   	this.wx=true;
   },
       //下拉加载更多
   	 menu(){    
 	var that=this;	
 	      	 var doe = document.documentElement || document.body,
                scrollHeight = doe.scrollHeight,
                clientHeight = doe.clientHeight,
                scrollY = window.scrollY || doe.scrollTop,
                offset = scrollHeight - scrollY - clientHeight;
                if(offset<300 ){
                	var datas;
                	if(that.flag){
                		var currentPage;
                		if(that.type=="AGENT"){
                				that.currentPage1++;
                				currentPage=that.currentPage1;
                		}else{
                			that.currentPage2++;
                			currentPage=that.currentPage2;
                		}
 		        that.flag=false;
                var datas={"type":that.type,"currentPage":currentPage,"perPageNum":"10",}
	 	get("/wc_app/query_team",datas).then( res=>{		
			if(res.data.data.list!=null){
				if(that.type=="AGENT"){
					that.dl=that.dl.concat(res.data.data.list)
				}else{
					that.kf=that.kf.concat(res.data.data.list);
				}
			}else{
				that.flag=false;  
			}
			that.flag=true;
	})
                }}
   	 }
  }
}
</script>

<style scoped lang="less">
.px2rem(@name, @px){@{name}: @px/75*1rem;}
.team{
width:100vw;
  min-height:100vh;
 margin:0 auto;
}
.capital_box{
  width:100%;
  height:auto;
  overflow: hidden;
  background-color:#ffffff;
  .capital_box_nav{
  .px2rem(width, 460);
    .px2rem(height, 54);
    margin:0 auto;
    border-radius: 8px;
    box-sizing: border-box;
    border:1px solid #007AFF;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    .px2rem(margin-top,50);
    .px2rem(margin-bottom,50);
    box-sizing: border-box;
    span{
      width:50%;
      height:100%;
      display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .px2rem(font-size, 26);
    color:#007AFF;
    }
    .activespan{
      color:#ffffff;
      background-color:#007AFF;
    }
  }
}
.team_box_ball{
  width:100%;
  height:auto;
  overflow: hidden;
   background-color:#ffffff;
   .px2rem(margin-top, 20);
}
.team_box1{
  height:auto;
  background:#ffffff;
  display: flex;
  flex-direction: row;
  .px2rem(padding-top, 40);
}
 .team_box1_c{
  .px2rem(padding-bottom, 20);
   .px2rem(width, 400);
   height:auto;
   display: flex;
   flex-direction: column;
   justify-content: center;
   color:#666666;
   span{
      .px2rem(font-size,24);
   }
   .span1{
        color:#2E2E2E;
        font-family: 'PingFangSC-Medium';
        .px2rem(font-size,32);
        display: inline-block;
   }
 }
.header_img{
   .px2rem(width, 120);
    .px2rem(height, 120);
    display: block;
    border-radius: 50%;
    .px2rem(margin-left, 30);
    .px2rem(margin-right, 20);
 } 
  .wximg{
    .px2rem(width, 60);
    .px2rem(height, 60);
    display: block;
    border-radius: 50%;
    .px2rem(margin-top, 30);
 }
.callimg{
   .px2rem(margin-right, 40);
    .px2rem(margin-left, 30);
}
.team_box2{
.px2rem(margin-left, 170);
 .px2rem(padding-top, 26);
 .px2rem(padding-bottom, 40);
 .px2rem(width, 580);
 border-top:1px solid #d1d1d6;
}
.team_box2_1{
  color:#666666;
  .px2rem(font-size, 24);
  display: flex;
  flex-direction: column;
  .px2rem(padding-left, 110);
  position: relative;
  i{
    font-style: normal;
    position: absolute;
    top:0;left:0;
  }
}
.team_box_list{
  display: flex;
  flex-direction: row;
  background:#ffffff;
  border-top:1px solid #d1d1d6;
   .px2rem(padding-top,40);
 .px2rem(padding-bottom,40);
  .callimg{
    .px2rem(margin-top, 75);
    .px2rem(margin-left, 100);
  }
  .team_box1_c{
    padding:0;
  }
}
.team_box_list:nth-last-of-type(1){
  border-bottom:1px solid #d1d1d6;
}
.qr{
	position: fixed;
	left:50%;
	top:50%;
	transform: translate(-50%,-50%);
	.px2rem(width,670);
	.px2rem(height,1028);
	background-color:#ffffff;
	border-radius: 20px;
	overflow: hidden;
	.qr1{
		width:100%;
		text-align: center;
		.px2rem(padding-top,86);
		.px2rem(padding-bottom,48);
		color:#000000;
		.px2rem(font-size,40);
	}
	.qr_img{
		.px2rem(height,530);
		.px2rem(width,530);
		margin:0 auto;
		display: block;;
	}
	.qr_bc{
		position: absolute;
		bottom:0;
		left:0;
		width:100%;
		.px2rem(height,100);
		.px2rem(line-height,100);
		text-align: center;
		background-color:#007AFF;
		 color:#ffffff;
     .px2rem(font-size, 34);
	}
}
.team_a{
	position: fixed;
}
.close{
	position: absolute;
	right:20px;
	top:20px;
}
.no_dl{
	width:100vw;
	height:100vh;
	background:#ffffff;
	position: fixed;
	left:0;.px2rem(top,245);
	border-top:1px solid #D1D1D6;
	span,a{
		display: block;
		text-align: center;
		color:8E8E93;
		font-size: 20px;
	}
	.span_1{
		font-size: 18px;
		padding-top:80px;	
		/*padding-bottom:10px;*/	
	}
	.span_2{
		font-size: 13px;
		padding-bottom:20px;	
	}
	a{
		color:#0078FF;
		font-size: 13px;
	}
}
</style>



